.body-position {
    position: sticky;
    top: 10vh;
}

.blank-body {
    background-color: azure;

}

.nonblank-body {
    display: flex;
    background-color: #979aaa;
    flex-direction: column;
    justify-content: space-around;
    align-items: stretch;
    border: 1px solid black;
    border-radius: 5px;
    border-style: groove;
    padding: 10px;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
    height: auto;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.email-information {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 25px;
    padding-left: 200px;
    padding-bottom: 50px;
    font-weight: 700;
}

.label {
    font-weight: 700;
    font-size: larger;
}

.message-body {
    background: #dbd4de;
    width: 50vw;
    height: 500px;
    padding: 5px;
    border: 3px solid black;
    border-style: double;
    padding-top: 5px;
    font-weight: 500;
    text-align: justify;
    overflow: scroll;
}
