.emaillist {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    border: 1px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
    padding: 5px;
}

@media only screen and (max-width: 815px) {
    .cardlist {
        margin: 0 auto;
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 20px;
    }
}