.App {
  display: flex;
  text-align: center;
  border: 2px solid black;
  height: auto;
  position: absolute;
  justify-content: center;
  background: #4c516d;

}

.Folder {
  border-right: 2px solid black;
  border-left: 2px solid black;
  margin: 2px;
  padding: 10px;
  width: 3vw;  
}

.SideBar {
  border-left: 2px solid black;
  border-right: 2px solid black;
  margin: 2px;
  padding: 10px;
  width: 20vw;
}

.EmailBody {
  border-left: 2px solid black;
  margin: 2px;
  padding: 10px;
  width: 70vw;
}

.FolderText {
  padding-top: 100px;
  font-size: larger;
  font-weight: 500;
}
